<template>
  <div class="expand">
    <router-link class="overlay" to="/"></router-link>
    <div id="container" style="height: 100%; width: 100%"></div>
  </div>
</template>

<script>
import axios from 'axios';
import Highcharts from 'highcharts/highstock';
import darkUnica from '@/assets/dark-unica';

export default {
  name: 'Chart',
  props: ['hash'],
  data() {
    return {
      chart: null,
    };
  },
  created() {
    this.setupChart();
    this.setupTheme();
  },
  mounted() {
    this.fetchChart(this.hash);
  },
  methods: {
    setupChart() {
      Highcharts.setOptions({
        lang: {
          loading: 'Descargando grafico...',
          weekdays: ['Domingo', 'Lunes', 'Martes', 'Miercoles', 'Jueves', 'Viernes', 'Sabado'],
          rangeSelectorFrom: 'De',
          rangeSelectorTo: 'A',
          rangeSelectorZoom: 'Periodo',
        },
        global: {
          timezoneOffset: 3 * 60,
        },
      });
    },
    setupTheme() {
      darkUnica(Highcharts);
    },
    fetchChart(hash) {
      // create the chart
      const options = {

        loading: {
          hideDuration: 0,
          showDuration: 100,
          style: {
            backgroundColor: 'black',
          },
        },

        tooltip: {
          pointFormat: '{series.name}: {point.y:.0f}',
        },

        xAxis: {
          type: 'datetime',
          allowDecimals: false,
          showEmpty: false,
          tickPosition: 'inside',
          labels: {
            y: -15,
          },
        },

        yAxis: {
          gridLineWidth: 0,
          minorGridLineWidth: 0,
          allowDecimals: false,
          showEmpty: false,
          tickPosition: 'inside',
          labels: {
            x: -2,
          },
        },

        chart: {
          // Edit chart spacing
          spacing: [0, 0, 0, 0],
          // margin: 0,
          backgroundColor: null,
        },

        // Explicitly tell the width and height of a chart
        width: null,
        height: null,

        // Disable features
        exporting: {
          enabled: false,
        },
        credits: {
          enabled: false,
        },
        navigator: {
          enabled: false,
        },
        scrollbar: {
          enabled: false,
        },
        rangeSelector: {
          enabled: false,
        },
      };

      this.chart = Highcharts.stockChart('container', options);
      this.chart.showLoading();

      const url = `https://friendpaste.com/${hash}/raw`;
      axios.get(url).then((response) => {
        const data = response.data;
        const newData = [];
        let maxDate = null;
        let maxValue = 0;
        let minDate = null;
        let minValue = 9999;
        data.split('\n').forEach((value) => {
          const parts = value.split(',');
          const date = parts[0];
          let users = +parts[1];
          if (Number.isNaN(users)) users = 0;
          newData.push([date * 1000, users < 0 ? null : users]);
          if (users >= 0) {
            if (users > maxValue) {
              maxValue = users;
              maxDate = new Date(date * 1000);
            }
            if (users < minValue) {
              minValue = users;
              minDate = new Date(date * 1000);
            }
          }
        });
        this.chart.hideLoading();
        this.chart.addSeries({
          data: newData,
          name: 'Usuarios',
          type: 'area',
          step: true,
          gapSize: 5,
          tooltip: {
            valueDecimals: 2,
          },
          fillColor: {
            linearGradient: {
              x1: 0,
              y1: 0,
              x2: 0,
              y2: 1,
            },
            stops: [
              [0, Highcharts.getOptions().colors[0]],
              [1, Highcharts.Color(Highcharts.getOptions().colors[0]).setOpacity(0).get('rgba')],
            ],
          },
        });
        const plotOptions = {
          color: 'white',
          dashStyle: 'dash',
          width: 1,
          label: {
            verticalAlign: 'top',
            textAlign: 'left',
            style: {
              color: 'white',
            },
          },
          zIndex: 10,
        };
        plotOptions.value = maxDate;
        plotOptions.label.text = 'Hora pico';
        this.chart.xAxis[0].addPlotLine(plotOptions);
        plotOptions.value = minDate;
        plotOptions.label.text = 'Hora tranqui';
        this.chart.xAxis[0].addPlotLine(plotOptions);
      });
    },
  },
};
</script>

<style lang="scss">

  li > .expand {
    width  : 100%;
    height : 100%;
  }

  .mg-x-axis text {
    font-size : 1.2rem !important;
  }

  div#chart {
    flex-grow : 1;
  }

  .mg-missing .mg-main-line {
    opacity : .5;
  }

  .mg-missing .mg-main-area {
    opacity : .15;
  }

  .mg-missing-pane {
    transform : translateX(-28px) translateY(8px) scaleX(1.0208);
  }

</style>



