<template>
  <ul id="server-list">
    <Server v-for="(server, index) in servers" :key="server.hash" :server="server" :index="index">

    </Server>
  </ul>
</template>

<script>
import axios from 'axios';
import Server from './Server.vue';

export default {
  name: 'Home',
  components: {
    Server,
  },
  data() {
    return {
      servers: [],
    };
  },
  created() {
    const hash = '5bgDNldBpxfKtQcMd6aSrc';
    const url = `https://friendpaste.com/${hash}/raw`;
    axios.get(url).then((response) => {
      this.servers = response.data;
    });
  },
};
</script>

<style lang="scss">

  ul#server-list {
    list-style-type : none;
    padding         : 0;
    margin          : 0;
  }

</style>



