<!--suppress CssUnknownTarget -->
<template>
  <li>
    <template v-if="$route.params.id != server.hash">
      <router-link :to="server.hash" class="server-logo">
        <img :src="server.image"/>
      </router-link>
      <div class="overlay ranking"><p>{{ index + 1 }}</p></div>
      <ServerStats :server="server"></ServerStats>
    </template>
    <template v-else>
      <Chart :hash="server.hash"></Chart>
    </template>
  </li>
</template>

<script>
import Chart from './Chart.vue';
import ServerStats from './ServerStats.vue';

export default {
  name: 'Server',
  components: {
    Chart,
    ServerStats,
  },
  props: ['server', 'index'],
};
</script>

<style lang="scss">

  @import url('https://fonts.googleapis.com/css?family=Ubuntu+Mono');

  ul#server-list li {
    margin      : 0 0 20px 0;
    height      : 160px;
    background  : url("~@/assets/pattern-frame.png");
    position    : relative;
    align-items : center;
    display     : flex;
    padding     : 7px;
    color       : #aeaeae;
  }

  /* center block when only showing image */
  @media (max-width : 33.9em) {
    ul#server-list {
      text-align : center;
    }

    ul#server-list li a.server-logo {
      max-width : 100%;
    }
  }

  @media (max-width : 23.9em) {
    ul#server-list li {
      padding : 0;
      margin  : 0;
    }
  }

  /* To make the border above children http://stackoverflow.com/a/15223089 */
  @media (min-width : 23.9em) {

    ul#server-list li {
      box-shadow : 10px 10px 23px -2px rgba(0, 0, 0, 0.75);
    }

    ul#server-list li:after {
      content        : "";

      /* Expand element */
      position       : absolute;
      left           : 0;
      top            : 0;
      right          : 0;
      bottom         : 0;
      box-shadow     : inset 0px 0px 2px 2px #000,
      inset 0px 0px 0px 5px #620,
      inset 0px 0px 2px 7px #000;

      /* Disable click events */
      pointer-events : none;
    }

    ul#server-list li:first-child:after {
      box-shadow : inset 0px 0px 2px 2px #000,
      inset 0px 0px 0px 5px #ff9901,
      inset 0px 0px 2px 7px #000;
    }

    ul#server-list li:nth-child(2):after {
      box-shadow : inset 0px 0px 2px 2px #000,
      inset 0px 0px 0px 5px #99b,
      inset 0px 0px 2px 7px #000;
    }
  }

  // number
  ul#server-list li p {
    margin      : 0;
    padding     : 0 0 3px 3px;
    font-size   : 4em;
    color       : #fff;
    font-family : "Ubuntu Mono", sans-serif;
    text-shadow : 0 1px 0 #ccc,
    0 2px 0 #c9c9c9,
    0 3px 0 #bbb,
    0 4px 0 #b9b9b9,
    0 5px 0 #aaa,
    0 6px 1px rgba(0, 0, 0, .1),
    0 0 5px rgba(0, 0, 0, .1),
    0 1px 3px rgba(0, 0, 0, .3),
    0 3px 5px rgba(0, 0, 0, .2),
    0 5px 10px rgba(0, 0, 0, .25),
    0 10px 10px rgba(0, 0, 0, .2),
    0 20px 20px rgba(0, 0, 0, .15);
  }

  ul#server-list li .overlay {
    position    : absolute;
    display     : flex;
    align-items : center;
    width       : calc(100% - 14px);
    height      : 160px;
    color       : inherit;
  }

  ul#server-list li .overlay.ranking {
    align-items    : flex-end;
    pointer-events : none;
    color          : #5b80b2;
  }

  // server-image
  ul#server-list a.server-logo {
    background : url("~@/assets/background.png") center no-repeat black;
    width      : 100%;
    height     : 160px;
    min-width  : 320px;
    max-width  : 320px;
    display    : inline-block;
    text-align : center;
  }

  /* Disable pointer on image when showing full info */
  @media (min-width : 33.9em) {
    ul#server-list a.server-logo {
      /*pointer-events: none;*/
    }
  }

  @media (max-width : 61.9em) {
    ul#server-list a.server-logo {
      min-width : 130px;
    }
  }

  ul#server-list img {
    max-width  : 80%;
    max-height : 80%;
  }

  /* vertical align http://stackoverflow.com/a/7310398 */

  ul#server-list a.server-logo:before,
  .serverlist_before {
    content        : "";
    display        : inline-block;
    height         : 100%;
    vertical-align : middle;
  }

  ul#server-list img {
    vertical-align : middle;
  }

  /* Move this to conditional comments */
  ul#server-list a.server-logo {
    list-style : none;
    behavior   : expression(
        function(t){
            t.insertAdjacentHTML('afterBegin','<span class="serverlist_before"></span>');
            t.runtimeStyle.behavior = 'none';
        }(this)
    );
  }

  /** end vertical align **/
</style>



