<template>
  <div id="main">
    <div id="content">
      <p class="tooltip">Haz click en los logos para ver el grafico</p>
      <ServerList></ServerList>
      <div class="fb-comments" data-colorscheme="dark" data-width="100%" data-href="https://argentumland.github.io/" data-numposts="20"></div>
    </div>
  </div>
</template>

<script>
import ServerList from './ServerList.vue';

export default {
  components: {
    ServerList,
  },
};
</script>

<style lang="scss">
  #main {
    display         : flex;
    justify-content : center;
    padding         : 0 20px 0;
    flex-wrap       : wrap;
  }

  .tooltip {
    color: white;
    font-family : 'Ubuntu Mono', monospace;
  }

  #main > div {
    margin : 0 20px 0;
  }

  @media (max-width : 23.9em) {
    #main {
      padding : 0;
    }

    #main > div {
      margin : 0;
    }
  }

  #content {
    flex      : 1;
    max-width : 800px;
  }

</style>



