<template>
  <div class="stats">
    <div class="labels">
      <div>Nombre</div>
      <div>Usuarios online</div>
      <div>Hora pico</div>
      <div>Hora tranqui</div>
      <div>Web</div>
      <div>Foro</div>
      <div>Facebook</div>
    </div>
    <div class="data">
      <div>{{ server.name }}</div>
      <div>{{ server.users | parseUsers }}</div>
      <div>{{ server.peak | getTime }}</div>
      <div>{{ server.nadir | getTime }}</div>
      <div><a :href="server.web">{{ server.web }}</a></div>
      <div><a :href="server.board">{{ server.board }}</a></div>
      <div><a :href="'http://www.facebook.com/' + server.facebook">{{ server.facebook }}</a></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ServerStats',
  props: ['server'],
  filters: {
    parseUsers: v => (Number.isNaN(+v) ? 'No disponible' : v),
    getTime: function getTime(v) {
      const date = new Date(v * 1000);
      // eslint-disable-next-line prefer-template
      return ('0' + date.getHours()).slice(-2) + ':' + ('0' + date.getMinutes()).slice(-2);
    },
  },

};
</script>

<style lang="scss">

  ul#server-list a {
    color           : #fff;
    text-decoration : none;
  }

  ul#server-list a:hover {
    color : inherit;
  }

  ul#server-list li .stats {
    flex-grow    : 1;
    display      : flex;
    padding-left : 20px;
    text-align   : left;
    font-family  : "Ubuntu Mono", sans-serif;
    min-width    : 368px;
  }

  ul#server-list li .stats div {
    white-space : nowrap;
    overflow    : hidden;
  }

  ul#server-list li .stats div {
    padding : 0 0 0 4px;
  }

  ul#server-list li .stats .labels {
    margin      : 0 10px 0 0;
    font-weight : bold;
    flex        : 0 0 auto;
  }

  @media (max-width : 33.9em) {
    ul#server-list li .stats {
      display : none;
    }
  }

  @media (max-width : 33.9em) {
    ul#server-list li .stats.mini {
      display   : flex;
      min-width : 0;
      max-width : 320px;
      font-size : .8em;
    }
  }

</style>



